import React from "react";
import s from './MyPosts.module.css';
import Post from "./Post/Post";
import {Field, reduxForm} from "redux-form";
import {maxLengthCreator, minLengthCreator, required} from "../../../utils/validators";
import {Textarea} from "../../common/FormsControls/FormsControls";

const maxLength30 = maxLengthCreator(30);
const minLength3 = minLengthCreator(3);

const AddNewPostForm = (props) => {
    return(
        <form onSubmit={props.handleSubmit}>
            <div>
                <Field name="newPostText" component={Textarea} placeholder="enter your post"
                       validate={[required, maxLength30, minLength3]} />
            </div>
            <div>
                <button>Add post</button>
            </div>
        </form>
    )
}
let AddNewPostFormRedux = reduxForm({form: "ProfileAddNewPostForm"})(AddNewPostForm);


const MyPosts = React.memo(props => {
    console.log("RENDER")


    let postsElement =
        [...props.posts]
            .reverse()
            .map(p => <Post message={p.message} key={p.message} likeCount={p.likeCount}/>)

    let newPostElement = React.createRef();

    let onAddPost = (values) => {
        props.addPost(values.newPostText);
    }

    return (

        <div className={s.postBlock}>
            <h3>my posts</h3>
            <AddNewPostFormRedux onSubmit={onAddPost}/>
            <div className={s.posts}>
                {postsElement}
            </div>

        </div>
    );
});


export default MyPosts;