import React from "react";
import s from './Post.module.css';


const Post = (props) => {
    return (
        <div className={s.item}>
            <img src="https://video-editor.su/images/kak-snimalsya-film-avatar_01.jpg" alt="profile"/>
            {props.message}
            <div>
                <span>Likes</span> {props.likeCount}
            </div>
        </div>
    );
}

export default Post;